          </el-col>
          <el-col class="hidden-sm-and-down" :md="6" :lg="6" :xl="6"></el-col>
        </el-row>
      </header>
      <main>
        <div id="dashboard" v-if="childPath()">
          <el-row :gutter="20">
            <el-col :span="24">
              <h1 class="pageTitle">Dashboard</h1>
            </el-col>
          </el-row>

          <el-row :gutter="20" class="icon-box">
            <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
              <div class="box highlighter green">
                <div class="icon-wrap rouded">
                  <i class="fas fa-coins"></i>
                </div>
                <div class="text-wrap">
                  <span class="hl-text">R$ {{ this.widgetsData.totalSalesAmount }},00</span>
                  <p class="label">Total de Vendas Neste Mês</p>
                </div>
              </div>
            </el-col>

            <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
              <div class="box highlighter purple">
                <div class="icon-wrap rouded">
                  <i class="fas fa-user"></i>
                </div>
                <div class="text-wrap">
                  <span class="hl-text">{{ this.widgetsData.totalLeads }}</span>
                  <p class="label">Leads Criados Neste Mês</p>
                </div>
              </div>
            </el-col>

            <el-col :xs="24" :sm="12" :md="12" :lg="8" :xl="8">
