<script>
import MenuItem from "@/components/restricted/Leftbar/MenuItem";
import MenuWidget from "@/components/restricted/MenuWidget/FastMenu";
import Searchbar from "@/views/restricted/Components/Searchbar";
// import Agendamentos from "@/components/restricted/Dashboard/Agendamentos";
// import PainelMetas from "@/components/restricted/Dashboard/PainelMetas";

export default {
  name: "Dashboard",
  data() {
    return {
      menuWidgetKey: "",
      widgetsData: {
        totalLeads: 0,
        totalSalesAmount: 0,
        totalDeals: 0,
      },
    };
  },
  components: { MenuItem, MenuWidget, Searchbar },
  methods: {
    forceRerender() {
      this.menuWidgetKey = this.$route.path;
    },
    loadWidgets() {
      var filter = [
        {
          field: "created_at",
          operator: "greater",
          value: "2020-07-31",
        },
        {
          field: "created_at",
          operator: "less",
          value: "2020-09-01",
        },
      ];
      var perPage = "-1";
      var optionsURL = encodeURIComponent(JSON.stringify(filter));

      this.$http
        .get("/leads/view?filter=" + optionsURL + "&per_page=" + perPage)
        .then((res) => {
          if (res.status == "200") {
            this.widgetsData.totalLeads = res.data.meta.total_leads;
          }
        });

      this.$http
        .get("/module/deals/view?filter=" + optionsURL + "&per_page=" + perPage)
        .then((res) => {
          if (res.status == "200") {
            this.widgetsData.totalDeals = res.data.meta.total;
          }
        });
    },
    logout() {
      localStorage.removeItem("token");
      this.$router.push("/");
    },
    childPath() {
      if (this.$route.name == "Dashboard") {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    this.loadWidgets();
  },
  watch: {
    $route(to) {
      this.forceRerender();
      if (to.name === "Dashboard") {
        this.loadWidgets();
      }
    },
  },
};
</script>

